.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.mapContainer {

  border: 3px solid rgb(149, 192, 149);
  height: 70vh;
}



.sidebarStyle {
  display: inline-block;
  top: 0;
  left: 0;
  margin: 12px;
  background-color: #ffffff;
  color: #000000;
  z-index: 1 !important;
  padding: 6px;
  font-weight: bold;
}

 .mapContainer2{
  border: 3px solid green;

 }